import { Component, OnInit } from '@angular/core';
import { ContactService } from 'src/app/contact.service';
import { Contact } from 'src/app/shared/contact';





@Component({
  selector: 'app-contacts',
  templateUrl: './contacts.component.html',
  styleUrls: ['./contacts.component.css'],
  providers: [ContactService]
})
export class ContactsComponent implements OnInit {
  
  contacts: Contact[]=[];
  contact: Contact;


FirstName:String;
LastName:String;
DOB:String;
AnniversayDate:String;
Address:String;
Phone:String;
Email:String;

  constructor(private contactService: ContactService) { }
  
  addAddress() {
    const newContact = {
      FirstName: this.FirstName,
      LastName: this.LastName,
      DOB: this.DOB, 
      AnniversayDate:this.AnniversayDate,
      Address:this.Address,
      Phone: this.Phone,
      Email: this.Email,
     
    }
    this.contactService.addAddress(newContact)
      .subscribe(contact => {
        this.contacts.push(contact);
        this.contactService.getAddress()
      .subscribe(contacts =>
        this.contacts = contacts);
      });
  }
  deleteAddress(id: any) {
    var contacts = this.contacts;
    this.contactService.deleteAddress(id)
      .subscribe(data => {
        if (data.m == 1) {
          for (var i = 0; i < contacts.length; i++) {
            if (this.contacts[i]._id == id) {
              contacts.splice(i, 1);
            }
          }
        }
        this.contactService.getAddress()
      .subscribe(contacts =>
        this.contacts = contacts);
      })
  }
  ngOnInit(): void {
    this.contactService.getAddress()
      .subscribe(contacts =>
        this.contacts = contacts);
  }
}